import {
    Component,
    h,
    Host,
    Prop,
    State,
    Listen,
    Element,
} from '@stencil/core';

/**
 * @category Navigation
 * @description Display nested sub-menus in the atui-sidebar.
 * Sub-menu's can be collapsed via atui-accordion-item when the parent sidebar is expanded.
 * Submenu content is collapsed and hidden when the parent sidebar is collapsed.
 * Menu indent styling is supported up to 3 levels.
 *
 * @slot submenu-content - To place the atui-sidebar-menuitem(s) in the sub menu
 * @slot submenu-hover-content - To place the atui-sidebar-menuitem(s) in the sub menu on collapsed mode
 */
@Component({
    tag: 'atui-sidebar-submenu',
    shadow: false,
})
export class AtuiSidebarSubmenuComponent {
    /**
     * Label to be displayed for the menu item
     */
    @Prop() label!: string;

    /**
     * Icon to be displayed with the label
     */
    @Prop() icon?: string;

    /**
     * Alert badge for the menu item
     */
    @Prop() badge?: string;

    /**
     * Will change the styling of the menuitem when set
     */
    @Prop() is_active?: boolean;

    @State() isSidebarOpen: boolean = false;
    @State() isAccordionOpen: boolean = false;
    @State() atuiAccordionItem: HTMLAtuiAccordionItemElement;
    @Element() el!: HTMLElement;

    constructor() {
        this.handleAtuiChange = this.handleAtuiChange.bind(this);
    }

    async componentDidLoad() {
        await this.updateInitialStates();
    }

    async componentDidUpdate() {
        await this.updateInitialStates();
    }

    private async updateInitialStates() {
        const accordionItem = this.el.querySelector('atui-accordion-item');
        if (accordionItem) {
            this.isAccordionOpen = await accordionItem.getIsOpen();
        }

        const parentSidebar = this.el.closest('atui-sidebar');
        if (parentSidebar) {
            this.isSidebarOpen = await (parentSidebar as any).getIsOpen();
        }
    }

    @Listen('atuiSidebarChange', { target: 'window' })
    handleAtuiChange(event: CustomEvent<boolean>) {
        const target = event.target as HTMLElement;

        const parentSidebar = this.el.closest('atui-sidebar');

        if (target === parentSidebar) {
            this.isSidebarOpen = event.detail;

            if (!this.isSidebarOpen && this.atuiAccordionItem) {
                this.atuiAccordionItem.closeAccordion();
            }
        }
    }

    @Listen('atuiAccordionChange', { target: 'window' })
    handleAtuiAccordionChange(event: CustomEvent<boolean>) {
        this.isAccordionOpen = event.detail;
    }

    private getIsActive() {
        if (this.isSidebarOpen) {
            return !this.isAccordionOpen && this.is_active;
        }
        return this.is_active;
    }

    render() {
        return (
            <Host>
                <atui-accordion-item
                    class={!this.isSidebarOpen ? 'hidden' : ''}
                    aria-hidden={!this.isSidebarOpen}
                    ref={(el) => (this.atuiAccordionItem = el)}
                    item_id="one"
                >
                    {(this.label || this.icon) && (
                        <atui-sidebar-menuitem
                            slot="accordion-trigger"
                            icon={this.icon}
                            label={this.label}
                            badge={this.badge}
                            is_active={this.getIsActive()}
                        >
                            {this.isSidebarOpen && (
                                <i
                                    slot="menu-item-actions"
                                    role="presentation"
                                    aria-hidden="true"
                                    class="material-icons text-icon-md group-data-[state=expanded]/accordion-item:rotate-180"
                                >
                                    keyboard_arrow_down
                                </i>
                            )}
                        </atui-sidebar-menuitem>
                    )}
                    <div slot="accordion-content">
                        <slot name="submenu-content"></slot>
                    </div>
                </atui-accordion-item>

                <atui-menu
                    class={this.isSidebarOpen ? 'hidden' : 'w-input-sm'}
                    aria-hidden={this.isSidebarOpen}
                    trigger="hover"
                    position="right"
                    align="start"
                    portal={true}
                >
                    <atui-sidebar-menuitem
                        class="pl-16"
                        slot="menu-trigger"
                        icon={this.icon}
                        badge={this.badge}
                        label={this.label}
                        is_active={this.getIsActive()}
                    ></atui-sidebar-menuitem>

                    <div slot="menu-content">
                        <slot name="submenu-hover-content"></slot>
                    </div>
                </atui-menu>
            </Host>
        );
    }
}
